import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import {
  Container,
  Grid,
  Typography,
  Box,
  Paper,
  Button,
  Chip,
  Divider,
  IconButton,
  CircularProgress,
  Alert,
  Card,
  CardMedia,
  Dialog,
  DialogContent,
  useMediaQuery,
  useTheme,
  Avatar
} from '@mui/material';
import {
  LocationOn as LocationIcon,
  Home as HomeIcon,
  Hotel as HotelIcon,
  Bathtub as BathtubIcon,
  DirectionsCar as CarIcon,
  Phone as PhoneIcon,
  Email as EmailIcon,
  Favorite as FavoriteIcon,
  FavoriteBorder as FavoriteBorderIcon,
  ArrowBack as ArrowBackIcon,
  ArrowForward as ArrowForwardIcon,
  Close as CloseIcon,
  Share as ShareIcon
} from '@mui/icons-material';
import { propertyService, favoritesService, userService } from '../services/api';
import { useAuth } from '../contexts/AuthContext';

// Format currency (VND)
const formatCurrency = (value: number) => {
  return new Intl.NumberFormat('vi-VN', {
    style: 'currency',
    currency: 'VND',
    minimumFractionDigits: 0
  }).format(value);
};

// Format area (m)
const formatArea = (value: number) => {
  return `${value} m`;
};

// Format date
const formatDate = (dateString: string | null | undefined) => {
  if (!dateString) return 'Khng c thng tin';
  
  try {
    const date = new Date(dateString);
    
    // Kim tra date c hp l khng
    if (isNaN(date.getTime())) {
      return 'Ngy khng hp l';
    }
    
    return new Intl.DateTimeFormat('vi-VN', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric'
    }).format(date);
  } catch (error) {
    console.error('Error formatting date:', error, dateString);
    return 'Ngy khng hp l';
  }
};

const PropertyDetailPage: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const { isAuthenticated } = useAuth();
  
  // State for property data
  const [property, setProperty] = useState<any>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  
  // State for image gallery
  const [images, setImages] = useState<string[]>([]);
  const [selectedImageIndex, setSelectedImageIndex] = useState<number>(0);
  const [openGallery, setOpenGallery] = useState<boolean>(false);
  
  // State for favorites
  const [isFavorite, setIsFavorite] = useState<boolean>(false);
  const [favoriteId, setFavoriteId] = useState<string | null>(null);
  const [favLoading, setFavLoading] = useState<boolean>(false);
  
  // Fetch property data
  useEffect(() => {
    const fetchPropertyData = async () => {
      setLoading(true);
      setError(null);
      
      try {
        if (!id) throw new Error('Khng tm thy m bt ng sn');
        
        const response = await propertyService.getProperty(id);
        
        if (response.success) {
          setProperty(response.data);
          
          // Set images
          const imageUrls = [
            response.data.featured_image,
            ...(response.data.images || []).map((img: any) => img.url)
          ].filter(Boolean);
          
          setImages(imageUrls);
          
          // Record view if authenticated
          if (isAuthenticated) {
            try {
              await userService.addRecentlyViewed(response.data.id);
            } catch (error) {
              console.error('Error recording view:', error);
            }
          }
          
          // Check if property is in favorites
          if (isAuthenticated) {
            try {
              const favoritesResponse = await favoritesService.getFavorites();
              if (favoritesResponse.success) {
                const favorite = favoritesResponse.data.find(
                  (fav: any) => fav.property_id === response.data.id
                );
                if (favorite) {
                  setIsFavorite(true);
                  setFavoriteId(favorite.id);
                }
              }
            } catch (error) {
              console.error('Error checking favorites:', error);
            }
          }
        } else {
          setError(response.message || 'Khng th ti thng tin bt ng sn');
        }
      } catch (err: any) {
        console.error('Error fetching property:', err);
        setError(err.message || 'C li xy ra khi ti d liu');
      } finally {
        setLoading(false);
      }
    };
    
    fetchPropertyData();
  }, [id, isAuthenticated]);
  
  // Handle favorite toggle
  const handleFavoriteToggle = async () => {
    if (!isAuthenticated) {
      // Redirect to login
      window.location.href = '/login';
      return;
    }
    
    setFavLoading(true);
    
    try {
      if (isFavorite && favoriteId) {
        // Remove from favorites
        const response = await favoritesService.removeFavorite(favoriteId);
        if (response.success) {
          setIsFavorite(false);
          setFavoriteId(null);
        }
      } else {
        // Add to favorites
        const response = await favoritesService.addFavorite(id || '');
        if (response.success) {
          setIsFavorite(true);
          setFavoriteId(response.data.id);
        }
      }
    } catch (error) {
      console.error('Error toggling favorite:', error);
    } finally {
      setFavLoading(false);
    }
  };
  
  // Image gallery navigation
  const handlePrevImage = () => {
    setSelectedImageIndex((prev) => (prev === 0 ? images.length - 1 : prev - 1));
  };
  
  const handleNextImage = () => {
    setSelectedImageIndex((prev) => (prev === images.length - 1 ? 0 : prev + 1));
  };
  
  // Handle image click to open gallery
  const handleImageClick = (index: number) => {
    setSelectedImageIndex(index);
    setOpenGallery(true);
  };
  
  // If loading
  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: '60vh' }}>
        <CircularProgress />
      </Box>
    );
  }
  
  // If error
  if (error || !property) {
    return (
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Alert severity="error" sx={{ mb: 2 }}>
          {error || 'Khng tm thy thng tin bt ng sn'}
        </Alert>
        <Button component={Link} to="/mua-ban" variant="outlined" startIcon={<ArrowBackIcon />}>
          Quay li danh sch
        </Button>
      </Container>
    );
  }
  
  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Grid container spacing={3}>
        {/* Left column: Images and details */}
        <Grid item xs={12} md={8}>
          {/* Property title and basic info */}
          <Typography variant="h4" component="h1" gutterBottom>
            {property.title}
          </Typography>
          
          <Box sx={{ display: 'flex', alignItems: 'center', mb: 2, color: 'text.secondary' }}>
            <LocationIcon fontSize="small" />
            <Typography variant="body1" sx={{ ml: 0.5 }}>
              {property.address}
            </Typography>
          </Box>
          
          {/* Featured image */}
          <Box sx={{ position: 'relative', mb: 2, borderRadius: 1, overflow: 'hidden' }}>
            <Box
              component="img"
              src={images[0] || 'https://via.placeholder.com/800x500?text=No+Image'}
              alt={property.title}
              sx={{
                width: '100%',
                height: 'auto',
                maxHeight: 500,
                objectFit: 'cover',
                cursor: 'pointer'
              }}
              onClick={() => handleImageClick(0)}
            />
            
            {/* Image count indicator */}
            {images.length > 1 && (
              <Chip
                label={`${images.length} hnh nh`}
                size="small"
                sx={{
                  position: 'absolute',
                  bottom: 16,
                  right: 16,
                  bgcolor: 'rgba(0, 0, 0, 0.6)',
                  color: 'white'
                }}
              />
            )}
          </Box>
          
          {/* Image thumbnails */}
          {images.length > 1 && (
            <Grid container spacing={1} sx={{ mb: 3 }}>
              {images.slice(0, 4).map((image, index) => (
                <Grid item xs={3} key={index}>
                  <Box
                    component="img"
                    src={image}
                    alt={`Thumbnail ${index + 1}`}
                    sx={{
                      width: '100%',
                      height: 80,
                      objectFit: 'cover',
                      borderRadius: 1,
                      cursor: 'pointer',
                      opacity: index === selectedImageIndex ? 0.7 : 1,
                      border: index === selectedImageIndex ? '2px solid primary.main' : 'none'
                    }}
                    onClick={() => handleImageClick(index)}
                  />
                </Grid>
              ))}
              {images.length > 4 && (
                <Grid item xs={3}>
                  <Box
                    sx={{
                      width: '100%',
                      height: 80,
                      display: 'flex',
                      justifyContent: 'center',
                      alignItems: 'center',
                      backgroundColor: 'rgba(0, 0, 0, 0.1)',
                      borderRadius: 1,
                      cursor: 'pointer'
                    }}
                    onClick={() => setOpenGallery(true)}
                  >
                    <Typography variant="body2">+{images.length - 4} hnh</Typography>
                  </Box>
                </Grid>
              )}
            </Grid>
          )}
          
          {/* Property details */}
          <Paper elevation={1} sx={{ p: 3, mb: 3 }}>
            <Typography variant="h5" gutterBottom>
              Thng tin bt ng sn
            </Typography>
            
            <Grid container spacing={2} sx={{ mb: 2 }}>
              <Grid item xs={6} sm={3}>
                <Typography variant="body2" color="text.secondary">Loi hnh</Typography>
                <Typography variant="body1">
                  {property.property_type === 'apartment' && 'Cn h chung c'}
                  {property.property_type === 'house' && 'Nh ring'}
                  {property.property_type === 'villa' && 'Bit th'}
                  {property.property_type === 'land' && 't nn'}
                  {property.property_type === 'office' && 'Vn phng'}
                  {property.property_type === 'shop' && 'Mt bng kinh doanh'}
                </Typography>
              </Grid>
              
              <Grid item xs={6} sm={3}>
                <Typography variant="body2" color="text.secondary">Din tch</Typography>
                <Typography variant="body1">{formatArea(property.area)}</Typography>
              </Grid>
              
              {property.bedrooms && (
                <Grid item xs={6} sm={3}>
                  <Typography variant="body2" color="text.secondary">Phng ng</Typography>
                  <Typography variant="body1">{property.bedrooms}</Typography>
                </Grid>
              )}
              
              {property.bathrooms && (
                <Grid item xs={6} sm={3}>
                  <Typography variant="body2" color="text.secondary">Phng tm</Typography>
                  <Typography variant="body1">{property.bathrooms}</Typography>
                </Grid>
              )}
            </Grid>
            
            <Divider sx={{ my: 2 }} />
            
            <Typography variant="h6" gutterBottom>
              M t
            </Typography>
            
            <Typography variant="body1" sx={{ whiteSpace: 'pre-line' }}>
              {property.description}
            </Typography>
            
            {/* Amenities */}
            {property.amenities && (
              <>
                <Typography variant="h6" sx={{ mt: 3, mb: 1 }}>
                  Tin ch
                </Typography>
                
                <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1 }}>
                  {property.amenities.split(',').map((amenity: string) => (
                    <Chip key={amenity} label={amenity.trim()} />
                  ))}
                </Box>
              </>
            )}
          </Paper>
        </Grid>
        
        {/* Right column: Price, contact, and sidebar info */}
        <Grid item xs={12} md={4}>
          {/* Price card */}
          <Paper elevation={2} sx={{ p: 3, mb: 3 }}>
            <Typography variant="h4" color="primary" gutterBottom>
              {formatCurrency(property.price)}
              {property.listing_type === 'rent' && '/thng'}
            </Typography>
            
            <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
              <HomeIcon sx={{ mr: 1, color: 'text.secondary' }} />
              <Typography variant="body1">
                {formatArea(property.area)}  {property.bedrooms} PN  {property.bathrooms} PT
              </Typography>
            </Box>
            
            <Divider sx={{ my: 2 }} />
            
            {/* Contact info */}
            <Typography variant="subtitle1" gutterBottom>
              Lin h ngi ng
            </Typography>
            
            <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
              <Avatar sx={{ mr: 1.5 }}>
                {property.owner && property.owner.name ? property.owner.name.charAt(0) : 'U'}
              </Avatar>
              <Box>
                <Typography variant="body1">
                  {property.owner && property.owner.name ? property.owner.name : 'Ch nh'}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  ng ngy {formatDate(property.created_at)}
                </Typography>
              </Box>
            </Box>
            
            {property.contact_info && (
              <Button
                fullWidth
                variant="contained"
                startIcon={<PhoneIcon />}
                sx={{ mb: 1.5 }}
              >
                {property.contact_info}
              </Button>
            )}
            
            <Button
              fullWidth
              variant="outlined"
              startIcon={<EmailIcon />}
              sx={{ mb: 2 }}
              component={Link}
              to={`/lien-he?property=${property.id}`}
            >
              Gi yu cu lin h
            </Button>
            
            <Button
              fullWidth
              variant={isFavorite ? "outlined" : "text"}
              color={isFavorite ? "primary" : "inherit"}
              startIcon={isFavorite ? <FavoriteIcon color="primary" /> : <FavoriteBorderIcon />}
              onClick={handleFavoriteToggle}
              disabled={favLoading}
            >
              {isFavorite ? ' lu' : 'Lu tin'}
            </Button>
          </Paper>
          
          {/* Location info */}
          <Paper elevation={1} sx={{ p: 3, mb: 3 }}>
            <Typography variant="h6" gutterBottom>
              V tr
            </Typography>
            
            <Box sx={{ display: 'flex', alignItems: 'center', color: 'text.secondary' }}>
              <LocationIcon fontSize="small" />
              <Typography variant="body2" sx={{ ml: 0.5 }}>
                {property.address}
              </Typography>
            </Box>
            
            {/* Map preview placeholder */}
            <Box
              sx={{
                mt: 2,
                height: 200,
                bgcolor: 'action.hover',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                borderRadius: 1
              }}
            >
              <Typography variant="body2" color="text.secondary">
                Bn 
              </Typography>
            </Box>
          </Paper>
          
          {/* Similar properties placeholder */}
          <Paper elevation={1} sx={{ p: 3 }}>
            <Typography variant="h6" gutterBottom>
              Bt ng sn tng t
            </Typography>
            
            <Typography variant="body2" color="text.secondary">
              ang ti...
            </Typography>
          </Paper>
        </Grid>
      </Grid>
      
      {/* Image gallery dialog */}
      <Dialog
        open={openGallery}
        onClose={() => setOpenGallery(false)}
        maxWidth="lg"
        fullScreen={isMobile}
      >
        <Box sx={{ position: 'relative' }}>
          <IconButton
            onClick={() => setOpenGallery(false)}
            sx={{ position: 'absolute', top: 8, right: 8, color: 'white', zIndex: 1 }}
          >
            <CloseIcon />
          </IconButton>
          
          <DialogContent sx={{ p: 0, position: 'relative' }}>
            <Box
              component="img"
              src={images[selectedImageIndex] || 'https://via.placeholder.com/800x500?text=No+Image'}
              alt={`Image ${selectedImageIndex + 1}`}
              sx={{
                width: '100%',
                height: 'auto',
                maxHeight: '80vh',
                objectFit: 'contain'
              }}
            />
            
            {/* Navigation arrows */}
            {images.length > 1 && (
              <>
                <IconButton
                  onClick={handlePrevImage}
                  sx={{
                    position: 'absolute',
                    top: '50%',
                    left: 16,
                    transform: 'translateY(-50%)',
                    bgcolor: 'rgba(0, 0, 0, 0.5)',
                    color: 'white',
                    '&:hover': { bgcolor: 'rgba(0, 0, 0, 0.7)' }
                  }}
                >
                  <ArrowBackIcon />
                </IconButton>
                
                <IconButton
                  onClick={handleNextImage}
                  sx={{
                    position: 'absolute',
                    top: '50%',
                    right: 16,
                    transform: 'translateY(-50%)',
                    bgcolor: 'rgba(0, 0, 0, 0.5)',
                    color: 'white',
                    '&:hover': { bgcolor: 'rgba(0, 0, 0, 0.7)' }
                  }}
                >
                  <ArrowForwardIcon />
                </IconButton>
                
                {/* Image counter */}
                <Box
                  sx={{
                    position: 'absolute',
                    bottom: 16,
                    left: '50%',
                    transform: 'translateX(-50%)',
                    bgcolor: 'rgba(0, 0, 0, 0.5)',
                    color: 'white',
                    px: 2,
                    py: 0.5,
                    borderRadius: 1
                  }}
                >
                  {selectedImageIndex + 1} / {images.length}
                </Box>
              </>
            )}
          </DialogContent>
        </Box>
      </Dialog>
    </Container>
  );
};

export default PropertyDetailPage; 