import React from 'react';
import { Link } from 'react-router-dom';
import { 
  Box, 
  Container, 
  Grid, 
  Typography, 
  Divider,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  IconButton,
  useTheme
} from '@mui/material';
import {
  Home as HomeIcon,
  Phone as PhoneIcon,
  Email as EmailIcon,
  LocationOn as LocationIcon,
  Facebook as FacebookIcon,
  YouTube as YouTubeIcon,
  Instagram as InstagramIcon,
  Twitter as TwitterIcon
} from '@mui/icons-material';

const Footer: React.FC = () => {
  const theme = useTheme();
  const currentYear = new Date().getFullYear();
  
  return (
    <Box 
      component="footer" 
      sx={{ 
        bgcolor: 'primary.main',
        color: 'white',
        pt: 6,
        pb: 3,
      }}
    >
      <Container maxWidth="lg">
        <Grid container spacing={4}>
          {/* About section */}
          <Grid item xs={12} sm={6} md={3}>
            <Typography 
              variant="h6" 
              component="div" 
              sx={{ 
                mb: 2,
                fontWeight: 'bold',
                position: 'relative',
                '&:after': {
                  content: '""',
                  display: 'block',
                  width: '40px',
                  height: '3px',
                  backgroundColor: theme.palette.secondary.main,
                  mt: 1
                }
              }}
            >
              V BSN Vit Nam
            </Typography>
            
            <Typography variant="body2" sx={{ mb: 2 }}>
              Chng ti cung cp nn tng ng tin bt ng sn uy tn, kt ni ngi mua v ngi bn, 
              gip bn tm kim bt ng sn ph hp hoc ng tin qung co hiu qu.
            </Typography>
            
            <Box sx={{ mt: 2 }}>
              <IconButton 
                aria-label="facebook" 
                size="small"
                sx={{ 
                  mr: 1, 
                  bgcolor: 'rgba(255,255,255,0.1)',
                  color: 'white',
                  '&:hover': {
                    bgcolor: 'secondary.main'
                  }
                }}
              >
                <FacebookIcon fontSize="small" />
              </IconButton>
              
              <IconButton 
                aria-label="twitter" 
                size="small"
                sx={{ 
                  mr: 1, 
                  bgcolor: 'rgba(255,255,255,0.1)',
                  color: 'white',
                  '&:hover': {
                    bgcolor: 'secondary.main'
                  }
                }}
              >
                <TwitterIcon fontSize="small" />
              </IconButton>
              
              <IconButton 
                aria-label="youtube" 
                size="small"
                sx={{ 
                  mr: 1, 
                  bgcolor: 'rgba(255,255,255,0.1)',
                  color: 'white',
                  '&:hover': {
                    bgcolor: 'secondary.main'
                  }
                }}
              >
                <YouTubeIcon fontSize="small" />
              </IconButton>
              
              <IconButton 
                aria-label="instagram" 
                size="small"
                sx={{ 
                  bgcolor: 'rgba(255,255,255,0.1)',
                  color: 'white',
                  '&:hover': {
                    bgcolor: 'secondary.main'
                  }
                }}
              >
                <InstagramIcon fontSize="small" />
              </IconButton>
            </Box>
          </Grid>
          
          {/* Links section */}
          <Grid item xs={12} sm={6} md={3}>
            <Typography 
              variant="h6" 
              component="div" 
              sx={{ 
                mb: 2,
                fontWeight: 'bold',
                position: 'relative',
                '&:after': {
                  content: '""',
                  display: 'block',
                  width: '40px',
                  height: '3px',
                  backgroundColor: theme.palette.secondary.main,
                  mt: 1
                }
              }}
            >
              Lin kt nhanh
            </Typography>
            
            <List disablePadding>
              {[
                { text: 'Trang ch', path: '/' },
                { text: 'Mua bn', path: '/mua-ban' },
                { text: 'Cho thu', path: '/cho-thue' },
                { text: 'Tin tc', path: '/tin-tuc' },
                { text: 'Gii thiu', path: '/gioi-thieu' },
                { text: 'Lin h', path: '/lien-he' },
              ].map((item) => (
                <ListItem 
                  key={item.path} 
                  component={Link} 
                  to={item.path}
                  disablePadding
                  sx={{
                    color: 'rgba(255,255,255,0.7)',
                    mb: 1,
                    transition: 'all 0.2s ease',
                    '&:hover': {
                      color: 'white',
                      pl: 1
                    }
                  }}
                >
                  <ListItemIcon sx={{ minWidth: 30, color: 'inherit' }}>
                    <HomeIcon fontSize="small" />
                  </ListItemIcon>
                  <ListItemText primary={item.text} />
                </ListItem>
              ))}
            </List>
          </Grid>
          
          {/* Contact section */}
          <Grid item xs={12} sm={6} md={3}>
            <Typography 
              variant="h6" 
              component="div" 
              sx={{ 
                mb: 2,
                fontWeight: 'bold',
                position: 'relative',
                '&:after': {
                  content: '""',
                  display: 'block',
                  width: '40px',
                  height: '3px',
                  backgroundColor: theme.palette.secondary.main,
                  mt: 1
                }
              }}
            >
              Lin h
            </Typography>
            
            <List disablePadding>
              <ListItem disablePadding sx={{ mb: 2 }}>
                <ListItemIcon sx={{ minWidth: 30, color: 'rgba(255,255,255,0.9)' }}>
                  <LocationIcon fontSize="small" />
                </ListItemIcon>
                <ListItemText 
                  primary="a ch" 
                  secondary="123 Nguyn Vn Linh, Qun 7, TP. H Ch Minh"
                  primaryTypographyProps={{ fontSize: 14, fontWeight: 'medium' }}
                  secondaryTypographyProps={{ fontSize: 14, color: 'rgba(255,255,255,0.7)' }}
                />
              </ListItem>
              
              <ListItem disablePadding sx={{ mb: 2 }}>
                <ListItemIcon sx={{ minWidth: 30, color: 'rgba(255,255,255,0.9)' }}>
                  <PhoneIcon fontSize="small" />
                </ListItemIcon>
                <ListItemText 
                  primary="in thoi" 
                  secondary="(028) 1234 5678"
                  primaryTypographyProps={{ fontSize: 14, fontWeight: 'medium' }}
                  secondaryTypographyProps={{ fontSize: 14, color: 'rgba(255,255,255,0.7)' }}
                />
              </ListItem>
              
              <ListItem disablePadding sx={{ mb: 2 }}>
                <ListItemIcon sx={{ minWidth: 30, color: 'rgba(255,255,255,0.9)' }}>
                  <EmailIcon fontSize="small" />
                </ListItemIcon>
                <ListItemText 
                  primary="Email" 
                  secondary="info@bdsn.vn"
                  primaryTypographyProps={{ fontSize: 14, fontWeight: 'medium' }}
                  secondaryTypographyProps={{ fontSize: 14, color: 'rgba(255,255,255,0.7)' }}
                />
              </ListItem>
            </List>
          </Grid>
          
          {/* Newsletter section */}
          <Grid item xs={12} sm={6} md={3}>
            <Typography 
              variant="h6" 
              component="div" 
              sx={{ 
                mb: 2,
                fontWeight: 'bold',
                position: 'relative',
                '&:after': {
                  content: '""',
                  display: 'block',
                  width: '40px',
                  height: '3px',
                  backgroundColor: theme.palette.secondary.main,
                  mt: 1
                }
              }}
            >
              Thi gian lm vic
            </Typography>
            
            <Typography variant="body2" paragraph>
              <strong>T2 - T6:</strong> 8:00 - 17:30
            </Typography>
            
            <Typography variant="body2" paragraph>
              <strong>T7:</strong> 8:00 - 12:00
            </Typography>
            
            <Typography variant="body2">
              <strong>CN:</strong> Ngh
            </Typography>
            
            <Typography 
              variant="h6" 
              component="div" 
              sx={{ 
                mt: 3,
                mb: 2,
                fontWeight: 'bold',
                position: 'relative',
                '&:after': {
                  content: '""',
                  display: 'block',
                  width: '40px',
                  height: '3px',
                  backgroundColor: theme.palette.secondary.main,
                  mt: 1
                }
              }}
            >
              Hotline h tr
            </Typography>
            
            <Typography 
              variant="h5" 
              sx={{ 
                color: theme.palette.secondary.main,
                fontWeight: 'bold'
              }}
            >
              1900 1234
            </Typography>
          </Grid>
        </Grid>
        
        <Divider sx={{ my: 4, borderColor: 'rgba(255,255,255,0.1)' }} />
        
        <Box sx={{ textAlign: 'center' }}>
          <Typography variant="body2" sx={{ opacity: 0.7 }}>
            &copy; {currentYear} BSN Vit Nam. Tt c cc quyn c bo lu.
          </Typography>
        </Box>
      </Container>
    </Box>
  );
};

export default Footer; 